import React from "react";
import "../style/Skills.css";
import Skill from "./Skill";
import html from "../img/html-5.svg";
import css from "../img/css-3.svg";
import js from "../img/js.svg";
import react from "../img/react.png";
import bootstrap from "../img/bootstrap.svg";
import sass from "../img/sass.svg";
import jquery from "../img/jquery2.png";
import redux from "../img/redux.png";
import git from "../img/git.png";
import gulp from "../img/gulp.svg";
import webpack from "../img/webpack.svg";
import npm from "../img/npm.svg";
import github from "../img/github.svg";
import firebase from "../img/firebase.png";
import jsx from "../img/jsx.png";
import python from "../img/python.svg";
import SematicUiReact from "../img/semantic-ui-react.png";
import yarn from "../img/yarn.png";
import es6 from "../img/es6.png";
import emmet from "../img/emmet.svg";

function Skills() {
  return (
    <div className="skills">
      <h3 className="skills-title">Skills</h3>
      <div className="container">
        <Skill
          imgs={[
            { id: "0", img: html, alt: "HTML5" },
            { id: "1", img: css, alt: "CSS3" },
            { id: "2", img: sass, alt: "SASS/SCSS" },
            { id: "3", img: bootstrap, alt: "Bootstrap" },
            { id: "4", img: SematicUiReact, alt: "Semantic UI React" }
          ]}
        />
        <Skill
          imgs={[
            { id: "5", img: js, alt: "JavaScript" },
            { id: "6", img: es6, alt: "EcmaScript 6" },
            { id: "7", img: jsx, alt: "JSX" },
            { id: "8", img: jquery, alt: "jQuery" },
            { id: "9", img: react, alt: "React.js" }
          ]}
        />
        <Skill
          imgs={[
            { id: "10", img: npm, alt: "npm" },
            { id: "11", img: yarn, alt: "yarn" },
            { id: "12", img: webpack, alt: "Webpack" },
            { id: "13", img: gulp, alt: "Gulp" },
            { id: "14", img: redux, alt: "Redux/React-redux" }
          ]}
        />
        <Skill
          imgs={[
            { id: "1", img: git, alt: "Git/Gitbash" },
            { id: "1", img: github, alt: "Github" },
            { id: "1", img: emmet, alt: "Emmet" },
            { id: "1", img: firebase, alt: "Firebase" },
            { id: "1", img: python, alt: "Python" }
          ]}
        />
      </div>
    </div>
  );
}

export default Skills;
